.services {
  padding-top: 120px;

  @include onTablet {
    padding-top: 148px;
  }

  &__info {
    max-width: 280px;

    @include onTablet {
      max-width: 264px;
    }

    @include onSmallDesktop {
      max-width: 292px;
    }

    @include onDesktop {
      max-width: 362px;
    }
  }

  &__title {
    font-family: "Poppins", sans-serif;
    color: $color-light-title;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__sabtitle {
    line-height: 42px;
    color: $title-color;
    margin-bottom: 24px;
  }

  &__text {
    font-size: 14px;
    line-height: 150%;
    color: $text-content-color;
    margin-bottom: 48px;
    min-width: 100%;

    @include onTablet {
      margin-bottom: 56px;
    }
  }

  &__card {
    background-color: $white-color;
    border-radius: 16px;
    padding: 32px 32px 28px;
    transition: box-shadow 0.3s;
    margin-bottom: 24px;

    &:hover {
      box-shadow: 0 0 15px $shadow-color;
    }

    &:nth-child(odd) {
        transform: translateY(0);

      @include onTablet {
        transform: translateY(-50%);
      }
    }
  }

  &__card-num {
    display: inline-block;
    padding: 2px 12px;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    background-color: #2060f61a;
    color: $blue-active-color;
    border-radius: 23px;
    margin-bottom: 24px;
  }

  &__card-desc {
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    color: $text-content-color;
    margin-bottom: 32px;
    width: 200px;
  }

  &__wrap-link {
    display: flex;
    align-items: center;
    gap: 0 16px;
  }

  &__card-link {
    position: relative;
    width: 40px;
    height: 40px;
    background-color: $blue-active-color;
    border-radius: 50%;
    background-image: url(../images/service-arrow.svg);
    background-position: center;
    background-repeat: no-repeat;
    transition: transform 0.3s;
    text-decoration: none;

    &::after {
      display: block;
      position: absolute;
      content: "Learn more";
      width: max-content;
      left: 100%;
      top: 50%;
      transform: translateY(-50%);
      padding-left: 16px;
      font-family: "Poppins", sans-serif;
      font-weight: 600;
      font-size: 15px;
      line-height: 22px;
      color: #334563;
    }

    &:hover {
      transform: scale(1.05);
    }
  }
}