.expertise {
  display: block;
  background-color: $white-color;
  border-radius: 30px;
  padding: 72px 0;

  @include onTablet {
    padding: 128px 0 134px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    max-width: 275px;
    margin: 0 auto;
    gap: 56px;

    @include onTablet {
      gap: 72px;
      max-width: 358px;
    }

    @include onSmallDesktop {
      flex-direction: row;
      justify-content: space-between;
      max-width: 1024px;
    }
  }

  &__item {
    text-align: center;
    
    @include onSmallDesktop {
      max-width: 214px;
    }

    @include onDesktop {
      max-width: 264px;
    }
  }

  &__title {
    font-weight: 400;
    font-size: 44px;
    line-height: 120%;
    margin-bottom: 56px;
    text-align: center;

    @include onTablet {
      font-weight: 600;
      font-size: 52px;
      line-height: 150%;
      margin-bottom: 64px;
    }
  }

  &__item-title {
    margin-bottom: 16px;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
    color: $title-color;
  }

  &__item-img {
    width: 184px;
    height: 166px;
    border-radius: 20px;
    margin-bottom: 32px;
    transition: transform 0.3s;

    @include onSmallDesktop {
      margin-bottom: 40px;
    }

    &:hover {
      transform: scale(1.05);
    }
  }

  &__item-text {
    font-family: "Open Sans", sans-serif;
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: $text-content-color;
  }
}