.vision {
  background-color: $white-color;

  @include onTablet {
    background-image: url(../images/Shapes.png);
    background-position: center;
    background-repeat: no-repeat;
  }
  
    &__container {
      padding: 72px 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 120px;

    @include onTablet {
      margin-bottom: 0;
      padding: 200px 0;
    }
  }

  &__title {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 44px;
    line-height: 120%;
    text-align: center;
    color: $title-color;
    margin-bottom: 24px;

    @include onTablet {
      font-size: 52px;
    }
  }

  &__description {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 150%;
  text-align: center;
  color: $text-content-color;
  margin-bottom: 56px;
  max-width: 754px;

    @include onTablet {
      margin-bottom: 64px;
    }

    @include onDesktop {
      font-size: 24px;
    }
  }

  &__button {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    width: 280px;
    height: 56px;

    @include onTablet {
      width: 264px;
      height: 56px;
    }
  }
}