.footer {
  padding: 80px 0;
  background-color: $main-dark-theme-color;
  border-radius: 30px 30px 0 0;

  @include onTablet {
    padding: 140px 0 80px;
  }

  @include onSmallDesktop {
    padding: 180px 0 80px;
  }

  &__nav {
    padding-top: 80px;

    @include onSmallDesktop {
      padding-top: 40px;
    }
  }
}
