.nav {
  &__list {
    display: flex;
    column-gap: 48px;

    &--footer {
      flex-direction: column;
      gap: 24px 0;

      @include onSmallDesktop {
        flex-direction: row;
      }
    }

    &--footer:first-child {
      gap: 49px 0;

      @include onTablet {
        gap: 25px 0;
      }

      @include onSmallDesktop {
        justify-content: space-between;
      }
    }

    &--footer-on-tablet {
      @include onTablet {
        flex-direction: row;
        gap: 39px;
      }

      @include onSmallDesktop {
        gap: 64px;
      }
    }

  }

  &__item {
    list-style: none;
  }

  &__link {
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    letter-spacing: 3px;
    border-bottom: 2px solid transparent;
    padding-bottom: 8px;

    @include hover (border-color, $blue-active-color);
  }
}