.about {
  background-color: $section-bg;

  &__content {
    margin: 0 auto;
    display: block;
    padding: 120px 0;
    max-width: 754px;
    text-align: center;
    color: $text-content-color;

    @include onTablet {
      padding: 148px 0;
    }
  }

  &__title {
    margin-bottom: 24px;

    @include onTablet {
      margin-bottom: 16px;
    }
  }

  &__desc {
    font-weight: 600;
    font-size: 22px;
    line-height: 150%;

    @include onTablet {
      font-size: 24px;
    }
  }
}