.testimonials {
  padding: 120px 0;

  @include onTablet {
    padding: 148px 0;
  }

  &__title {
    margin-bottom: 16px;
    color: $color-light-title;
    text-transform: uppercase;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__subtitle {
    color: $title-color;
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 56px;
    }

    @include onSmallDesktop {
      margin-bottom: 85px;
    }
  }

  &__cards {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onSmallDesktop {
      flex-direction: row;
      justify-content: center;
      gap: 20px;
    }

    @include onDesktop {
      gap: 30px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: $white-color;
    border-radius: 16px;
    padding: 32px 37px 32px;
    transition: all 0.3s;

    @include onTablet {
      padding: 32px 98px 32px;
    }

    @include onSmallDesktop {
      padding: 32px 42px 32px;
    }

    @include onDesktop {
      padding: 32px 56px 32px;
    }

    &:hover {
      box-shadow: 0 0 20px $shadow-color;
      transform: scale(1.05);
    }
  }

  &__card-img {
    z-index: 1;
    width: 109px;
    height: 109px;
    border-radius: 50%;

    @include onTablet {
      width: 140px;
      height: 140px;
    }

    @include onSmallDesktop {
      width: 109px;
      height: 109px;
    }
  }

  &__rectangle {
    z-index: 0;
    width: 109px;
    height: 44px;
    background-color: $blue-active-color;
    border-radius: 12px;

    @include onTablet {
      width: 139px;
      height: 56px;
    }

    @include onSmallDesktop {
      width: 109px;
      height: 44px;
    }

    @include onDesktop {
      width: 139px;
      height: 56px;
    }
    
    &--1 {
      transform: translate(-35%, 90%) rotate(-60deg);
    }

    &--2 {
      margin-bottom: 30px;
      transform: translate(35%, -90%) rotate(-60deg);
    }
  }

  &__card-text {
    color: $text-content-color;
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 13px;
    line-height: 150%;
    text-align: center;
    height: 80px;
    margin-bottom: 16px;

    &--client {
      color: $title-color;
      font-weight: 600;
      margin-bottom: 4px;
    }
  }

  &__card-title {
    font-family: "Poppins";
    font-weight: 700;
    font-size: 13px;
    color: $color-light-title;
    line-height: 20px;
    text-align: center;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__quote {
    margin-bottom: 10px;
    display: block;
    width: 20px;
    height: 18px;
    background-image: url(../images/testimonials/quote.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
}