.contact {
  background-color: $main-dark-theme-color;

  &__title {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 130%;
    color: $white-color;
    margin-bottom: 48px;
  }

  &__field {
    margin-bottom: 40px;

    &:last-of-type {
      margin-bottom: 56px;
    }

    display: block;
    width: 100%;
    padding-bottom: 16px;
    border: transparent;
    border-bottom: 1px solid $white-color;

    font-family: "Poppins";
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    color: $white-color;
    background: none;

    &::placeholder {
      color: $white-color;
      opacity: 0.7;
    }

    &:focus {
      border-bottom: 1px solid $blue-active-color;
      outline: none;
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus,
    &:-webkit-autofill:active {
      -webkit-box-shadow: 0 0 0 50px $main-dark-theme-color inset;
      -webkit-text-fill-color: $white-color;
    }
  }

  &__button {
    margin-bottom: 80px;
  }

  &__subtitle {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: $white-color;
    opacity: 0.5;

    margin-bottom: 8px;
  }

  &__link {
    display: block;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 150%;
    color: $white-color;
    text-decoration: none;

    margin-bottom: 40px;

    &:hover {
      color: $blue-active-color;
    }
  }

  &__wrap-socials {
    margin-top: 18px;

    display: flex;
    column-gap: 32px;
  }

  &__social {
    fill: #fff;
    transition: fill 0.3s;

    &:hover {
      fill: $blue-active-color;
      cursor: default;
    }

    &--fb {
      height: 24px;
      width: 14px;
    }

    &--tw {
      height: 21px;
      width: 24px;
    }

    &--inst {
      height: 24px;
      width: 24px;
    }
  }
}