.menu {
  height: 100vh;
  padding: 21px;
  overflow-y: auto;
  background-color: $main-dark-theme-color;

  @include onTablet {
    padding: 21px 42px;
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto 50px;
    background-size: contain;
    background-repeat: no-repeat;

    &--cross {
      background-image: url(../images/logo/icon-close.svg);
      background-size: cover;
      height: 22px;
      width: 22px;
      transition: transform 0.3s;

      &:hover {
        transform: scale(1.3);
      }
    }
  }

  &__list {
    text-align: center;

  }

  &__item {
    display: block;
    list-style: none;
    margin-bottom: 50px;

    &--link {
      text-decoration: none;
      font-size: 32px;
      color: $page-wight-color;
      font-weight: 600;
      line-height: 48px;
      border-bottom: 2px solid transparent;
      padding-bottom: 8px;

      @include hover (border-color, $blue-active-color);
    }
  }

}